<template>
  <div class="hots">
    <div class="gray-box">
      <div class="gray-box-title">热门商品</div>
      <div class="hots-item product-item" v-for="(i,index) in items" :key="index">
        <ProductItem :item="i"></ProductItem>
      </div>
    </div>
  </div>
</template>

<script>
  import ProductItem from "@/components/ProductItem"
  export default {
    props:["items"],
    components:{
      ProductItem
    }
  }
</script>

<style scoped>
  .hots{
    width:1220px;
    margin:0 auto;
  }
  .hots-item{
    float:left;
    height:430px;
    width:20%;
  }
</style>



