<template>
  <i class="user" title="" @click="toLogin">
    <div class="user-menu" @click="stopProp">
      <div class="user-menu-content">
        <div class="triangle user-triangle"></div>
        <div class="head-box">
          <img class="head-img" src="../../assets/img/defaultHead.jpg"><br>
          王二狗
        </div>
        <div class="user-item">
          我的订单
        </div>
        <div class="user-item">
          账号资料
        </div>
        <div class="user-item">
          收货地址
        </div>
        <div class="user-item">
          售后服务
        </div>
        <div class="user-item">
          我的优惠
        </div>
        <div class="user-item">
          退出
        </div>
      </div>
    </div>
  </i>
</template>

<script>
  export default {
    methods:{
      toLogin(){
        this.$router.push("/login");
      },
      stopProp(event){
        event.stopPropagation();
      }
    }
  }
</script>

<style scoped>
  .user {
    display: inline-block;
    width: 20px;
    height: 20px;
    background: url(../../assets/img/account.png) -155px 0 no-repeat;
    margin-left: 86px;
    position: relative;
    top: 2px;
    cursor:pointer;
  }

  .user:hover {
    background-position: -5px 0;
  }

  .user-menu{
    margin-top: 20px;
    padding-top: 16px;
    width:168px;
    position: absolute;
    left: -74px;
    display: none;
    cursor:auto;
  }

  .user:hover .user-menu{
    display: block;
  }

  .user-menu-content {
    position: relative;
    z-index:9999;
    box-shadow:0 20px 40px rgba(0, 0, 0, 0.15);
    line-height: 1em;
    text-align: center;
    color: #757575;
    font-size: 12px;
    width: 168px;
    height: auto;
    border-radius: 8px;
    background: #fff;
  }

  .head-box {
    height: 108px;
    padding-top: 18px;
  }

  .head-img {
    border-radius: 23px;
    width: 46px;
    height: 46px;
    margin-bottom: 12px;
  }

  .user-item {
    border-top: 1px solid #f5f5f5;
    height: 44px;
    line-height: 44px;
    cursor:pointer;
  }

  .triangle {
    display: inline-block;
    width: 20px;
    height: 8px;
    background: url(../../assets/img/account.png) -49px -43px no-repeat;
    margin-left: 30px;
    position: absolute;
    top: -8px;
    left: 44px;
  }

  .fixed .user:hover{
    background-position: -215px 0;
  }
</style>



