<template>
  <div class="product-item">
    <router-link :to="'/productDetail/'+item.productId"><img :src="$store.state.URL.IMG + productImage"></router-link>
    <div class="title">{{item.productName}}</div>
    <div class="sub-title">{{item.sub_title}}</div>
    <div class="price"><span class="yen">&yen;</span>&nbsp;{{item.salePrice}}</div>
    <div class="btn-group">
      <router-link :to="'/productDetail/'+item.productId"><Button theme="white">查看详情</Button></router-link>
      &nbsp;<Button theme="blue" @btn_click="addToCart">加入购物车</Button>
    </div>
  </div>
</template>

<script>
  export default {
    props: ["item"],
    methods:{
        addToCart(){
            let item = {};
            item.productId = this.item.productId;
            item.productImage = this.productImage;
            item.productName = this.item.productName;
            item.sub_title = this.item.sub_title;
            item.salePrice = this.item.salePrice;
            item.num = 1;
            this.$store.commit("addToCart",item);
        }
    },
    computed:{
      productImage(){
          let url = this.item.productImage;
          if(typeof(url) != "string"){
              return url[0];
          }else{
            return url;
          }
      }
    }
  }
</script>

<style scoped>
  .product-item {
    transition: transform 0.5s;
    height:100%;
  }

  .product-item:hover {
    transform: translate(0, -3px);
    box-shadow: 1px 1px 20px #999;
  }

  .product-item:hover .price{
    display:none;
  }

  .product-item:hover .btn-group{
    display:block;
  }

  .title {
    font-size: 16px;
    color: #424242;
    margin: 0 auto;
    padding: 2px 14px;
    text-align: center;
  }

  .sub-title {
    text-align: center;
    font-size: 12px;
    color: #d0d0d0;
    padding: 10px;
  }

  .price {
    text-align: center;
    height: 40px;
    line-height: 40px;
    color: #e4393c;
    font-size: 20px;
  }

  .yen {
    font-size: 16px;
  }

  img{
    margin:0 auto;
    padding-top: 50px;
    padding-bottom: 10px;
    width: 206px;
    height: 206px;
    display: block;
    box-sizing: content-box;
  }

  .btn-group{
    display:none;
    text-align: center;
    padding-top: 10px;
  }
</style>



