<template>
  <div class="products">
    <HeaderNav></HeaderNav>
    <div class="products-content">
      <div class="condition">
        <a href="javascript:" class="sort" :class="{active:sort == ''}" @click="changeSort('')">综合排序</a>
        <a href="javascript:" class="sort" :class="{active:sort == 'salePrice'}" @click="changeSort('salePrice')">价格从低到高</a>
        <a href="javascript:" class="sort" :class="{active:sort == '-salePrice'}" @click="changeSort('-salePrice')">价格从高到低</a>
        <input type="text" placeholder="价格" v-model="lowPrice">
        -
        <input type="text" placeholder="价格" v-model="highPrice">
        &nbsp;&nbsp;<Button theme="blue" width="80px" @btn_click="changePrice">确定</Button>
        <Pagination :maxPage="maxPage" :currentPage="page" @changePage="changePage" v-if="products.length > 0"></Pagination>
      </div>
      <div class="products-items" v-if="products.length > 0">
        <div class="products-item" v-for="(p,i) in products" :key="i">
          <ProductItem :item="p"></ProductItem>
        </div>
      </div>
      <div v-if="dataReady && products.length == 0" class="products-empty">
        没找到符合条件的商品！
      </div>
      <div v-if="!dataReady" class="products-empty">
        加载中...
      </div>
      <div class="products-footer">
        <Pagination :maxPage="maxPage" :currentPage="page" @changePage="changePage" v-if="products.length > 0"></Pagination>
      </div>
    </div>
  </div>
</template>

<script>
  import ProductItem from "@/components/ProductItem"
  import HeaderNav from "@/components/HeaderNav"
  import Pagination from "@/components/Pagination"
  import axios from "axios"

  function getProducts(vueObj){
    vueObj.dataReady = false;
    vueObj.products = [];
    vueObj.maxPage = 1;
    axios.get(`${vueObj.$store.state.URL.API}/products?page=${vueObj.page}&size=${vueObj.size}&sort=${vueObj.sort}&lp=${vueObj.lowPrice}&hp=${vueObj.highPrice}`).then((resp)=>{
      vueObj.products = resp.data.currentData;
      vueObj.maxPage = resp.data.maxPage;
      vueObj.dataReady = true;
    });
  }

  export default {
    data(){
      return{
        products:[],
        maxPage:1,
        page:this.$route.query.page || 1,
        size:this.$route.query.size || 20,
        sort:this.$route.query.sort || "",
        lowPrice:this.$route.query.lp || "",
        highPrice:this.$route.query.hp || "",
        dataReady:false
      }
    },
    components:{
      ProductItem,
      HeaderNav,
      Pagination
    },
    created(){
      getProducts(this);
    },
    beforeRouteUpdate(to,from,next){
      getProducts(this);
      next();
    },
    methods:{
      changePage(p){
        this.page = p;
        this.$router.push({path:"/products",query:{page:this.page,size:this.size,sort:this.sort,lp:this.lowPrice,hp:this.highPrice}});
      },
      changePrice(){
        let lp = this.lowPrice;
        let hp = this.highPrice;
        this.page = 1;
        this.$router.push({path:"/products",query:{page:this.page,size:this.size,sort:this.sort,lp,hp}});
      },
      changeSort(s){
        if(s == this.sort){
            return;
        }
        this.page = 1;
        this.sort = s;
        this.$router.push({path:"/products",query:{page:this.page,size:this.size,sort:this.sort,lp:this.lowPrice,hp:this.highPrice}});
      }
    }
  }
</script>

<style scoped>
  .products-items{
    overflow: auto;
    background: #fff;
  }
  .products-content{
    width:1220px;
    margin:0 auto 30px;
  }
  .condition{
    padding:10px 0;
  }
  .products-item{
    width:25%;
    height:430px;
    float:left;
  }
  .sort{
    color:#999;
    padding:0 10px;
  }
  .sort:hover{
    color:#5683ea;
  }
  .sort.active{
    color:#5683ea;
  }
  input{
    background: transparent;
    text-align: center;
    width:80px;
    height:30px;
    border-radius:4px;
    font-size: 12px;
    line-height: 30px;
    border: 1px solid #ccc;
  }
  .products-footer{
    padding:10px 0;
  }
  .products-empty{
    font-size:30px;
    text-align: center;
    color:#999;
    padding:160px 0;
    border-radius:10px;
    background: #fff;
  }
</style>



