<template>
  <div class="pagination">
    <div class="page page-up" :class="{disabled:current <= 1}" @click="pageUp">上一页</div>
    <div class="page page-down" :class="{disabled:current >= maxPage}" @click="pageDown">下一页</div>
    <div class="number-box">
      第<input type="number" min="1" :max="maxPage" v-model="toPage">页/共{{maxPage}}页
      <Button theme="gray" width="64px" @btn_click="pageTo">跳转</Button>
    </div>
  </div>
</template>

<script>
export default {
  props:["currentPage","maxPage"],
  data(){
    return{
      toPage:this.currentPage,
      current:this.currentPage
    }
  },
  methods:{
    pageUp(){
      if(this.current <= 1){
        return;
      }
      this.current --;
      this.$emit("changePage",this.current);
      this.toPage = this.current;
    },
    pageDown(){
      if(this.current >= this.maxPage){
        return;
      }
      this.current ++;
      this.$emit("changePage",this.current);
      this.toPage = this.current;
    },
    pageTo(){
        if(this.toPage > 0 && this.toPage <= this.maxPage && this.toPage != this.current){
          this.current = this.toPage;
          this.$emit("changePage",this.current);
        }
    }
  }
}
</script>

<style scoped>
  .pagination{
    display:inline-block;
    float:right;
  }

  .page{
    display: inline-block;
    height:30px;
    width:80px;
    font-size: 12px;
    line-height: 30px;
    text-align:center;
    border: 1px solid #d1d1d1;
    background-color: #fff;
    color: #646464;
    float:left;
    cursor: pointer;
  }

  .page.disabled{
    background-color: #eee !important;
    color:#b6b6b6 !important;
    cursor: not-allowed !important;
  }

  .page:hover{
    background-color: #ccc;
    color:#fff;
  }

  .page-up{
    border-radius:10px 0 0 10px;
  }

  .page-down{
    border-left:0;
    -webkit-border-radius: 0;
    -moz-border-radius: 0;
    border-radius: 0 10px 10px 0;
  }

  .number-box{
    float:left;
    padding-left:8px;
  }

  input{
    height:30px;
    width:52px;
    padding:2px;
    border:1px solid #ccc;
    text-align:center;
  }
</style>



