<template>
  <div class="recommend">
    <div class="gray-box">
      <div class="gray-box-title">
        {{item.title}}
      </div>
      <img :src="$store.state.URL.IMG + item.img">
      <div class="recommend-item product-item" v-for="(p,i) in item.products" :key="i">
        <ProductItem :item="p"></ProductItem>
      </div>
    </div>
  </div>
</template>

<script>
  import ProductItem from "@/components/ProductItem"
  export default {
    props:["item"],
    components:{
      ProductItem
    }
  }
</script>

<style scoped>
  .recommend{
    width:1220px;
    margin:0 auto;
  }
  img{
    width: 50%;
    height:430px;
    float:left;
    position: relative;
    z-index:1;
  }
  .recommend-item{
    width:25%;
    height:430px;
    float:left;
  }
</style>



