<template>
  <div class="header">
    <div class="header-content">
      <router-link to="/"><i class="global-logo"></i></router-link>
      <div class="right-box">
        <router-link to="/products">全部商品</router-link>
        <i class="separate"></i>
        <User></User>
        <Cart></Cart>
      </div>
    </div>
  </div>
</template>

<script>
  import User from "@/components/header/User"
  import Cart from "@/components/header/Cart"
  export default {
      components:{
        User,
        Cart
      }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .header {
    background: #1a1a1a;
    height: 100px;
  }

  .header-content {
    width: 1220px;
    margin: 0 auto;
  }

  .global-logo {
    display: inline-block;
    width: 50px;
    height: 40px;
    background: url(../assets/img/global-logo.png) no-repeat;
    background-size: 100%;
    margin-top: 30px;
  }

  .right-box {
    float: right;
    line-height: 100px;
    position: relative;
  }

  .separate {
    display: inline-block;
    height: 14px;
    width: 1px;
    background: #333;
    position: absolute;
    top: 43px;
    margin-left: 43px;
  }

  a{
    color:#c8c8c8;
  }

  a:hover{
    color:#fff;
  }
</style>



