<template>
  <div class="product-detail">
    <HeaderNav></HeaderNav>
    <div class="product-detail-content">
      <div class="basic gray-box">
        <ul>
          <li @click="changeImage(index)" v-for="(i,index) in detail.productImage" :key="index" :class="{'active':index == activeIndex}">
            <img :src="$store.state.URL.IMG + i" alt="" class="small-img">
          </li>
        </ul>
        <img :src="$store.state.URL.IMG + detail.productImage[activeIndex]" alt="" class="big-img">
        <div class="basic-info">
          <div class="title-box">
            <h4 class="title">{{detail.productName}}</h4>
            <div class="price"><span class="yen">&yen;</span>&nbsp;{{detail.salePrice}}</div>
            <div class="sub-title">{{detail.sub_title}}</div>
          </div>
          <div class="num">
            <span class="num-text">数量</span>
            <Button theme="circle" class="btn-num btn-num-sub" :class="{disabled:num <= 1}" @btn_click="num_sub"></Button>
            <input type="text" class="input-num" v-model="num">
            <Button theme="circle" class="btn-num btn-num-plus" @btn_click="num++"></Button>
          </div>
          <div class="buy">
            <Button theme="blue big" @btn_click="addToCart()">加入购物车</Button>
            <Button theme="white big">现在购买</Button>
          </div>
        </div>
      </div>
      <div class="gray-box">
        <div class="gray-box-title">产品信息</div>
          <div class="more-info-empty" v-if="!detail.productMsg">该产品暂无图文信息！</div>
          <div v-if="detail.productMsg">
            <img :src="$store.state.URL.IMG + detail.productMsg.url" alt="" :width="detail.productMsg.width / 2" :height="detail.productMsg.height / 2">
          </div>
      </div>
    </div>
  </div>
</template>

<script>
  import HeaderNav from "@/components/HeaderNav"
  import axios from "axios"

  export default {
    components:{
      HeaderNav
    },
    data(){
        return{
          activeIndex:0,
          detail:{
            productImage:[]
          },
          num:1
        }
    },
    methods:{
      addToCart(){
        if(parseInt(this.num) >= 1){
          let item = {};
          item.productId = this.detail.productId;
          item.productImage = this.detail.productImage[0];
          item.productName = this.detail.productName;
          item.sub_title = this.detail.sub_title;
          item.salePrice = this.detail.salePrice;
          //输入框内容转换为数字
          item.num = parseInt(this.num);
          this.$store.commit("addToCart",item);
        }
      },
      num_sub(){
        if(this.num <= 1){
          return;
        }
        this.num --;
      },
      changeImage(index){
          if(this.activeIndex != index){
            this.activeIndex = index;
          }
      }
    },
    created(){
      axios.get(`${this.$store.state.URL.API}/productDetail?pid=${this.$route.params.id}`).then((resp)=>{
        this.detail = resp.data;
      });
    },
    beforeRouteUpdate(to,from,next){
      axios.get(`${this.$store.state.URL.API}/productDetail?pid=${to.params.id}`).then((resp)=>{
        this.detail = resp.data;
      });
      next();
    }
  }
</script>

<style scoped>
  .product-detail-content{
    width:1220px;
    margin:0 auto;
    padding:20px 0 50px 0;
  }
  .basic{
    padding:60px;
    height:440px;
    box-sizing: content-box;
  }
  .small-img{
    width:54px;
    height:54px;
  }
  li{
    width:80px;
    height:80px;
    padding-top:13px;
    text-align:center;
    border:1px solid #eee;
    margin-top:10px;
    cursor:pointer;
  }
  li:first-of-type{
    margin:0;
  }
  li.active{
    border:3px solid #ccc;
    padding-top:11px;
  }
  ul{
    float:left;
  }
  .big-img{
    width:440px;
    height:440px;
    margin-left:20px;
    float:left;
  }
  .basic-info{
    float:left;
    margin-left:10px;
    width:450px;
  }
  .title-box{
    padding: 8px 8px 18px 10px;
  }
  .title{
    font-size:24px;
    color:#000;
  }
  .sub-title{
    font-size: 14px;
    margin-top:13px;
    color: #bdbdbd;
  }
  .price {
    text-align: center;
    height: 40px;
    line-height: 40px;
    color: #e4393c;
    font-size: 20px;
    float:right;
  }
  .yen {
    font-size: 16px;
  }
  .num{
    padding: 29px 0 8px 10px;
    border-top: 1px solid #ebebeb;
    overflow: auto;
  }
  .num-text{
    padding-right: 20px;
    font-size: 14px;
    color: #8d8d8d;
    line-height: 37px;
    float:left;
  }
  .btn-num{
    float:left;
    background: url(../assets/img/button.jpg) no-repeat;
  }
  .btn-num-sub{
    background-position: 0 -60px;
  }
  .btn-num-sub:hover{
    background-position: 0 -180px;
  }
  .btn-num-sub.disabled{
    background-position: 0 -300px !important;
  }
  .input-num{
    width:36px;
    height:34px;
    background: transparent;
    text-align: center;
    float:left;
  }
  .btn-num-plus:hover{
    background-position: 0 -120px;
  }
  .btn-num-plus.disabled{
    background-position: 0 -240px !important;
  }
  .buy{
    border-top: 1px solid #ebebeb;
    padding: 30px 0 0 10px;
  }
  .more-info-empty{
    font-size:30px;
    text-align: center;
    color:#999;
    padding:160px 0;
  }
</style>



