<template>
  <div id="app">
    <Header v-if="showHeaderAndFooter"></Header>
    <!--吸顶时防止页面上移-->
    <div :class="{fixedPT:$store.state.fixed}" v-if="showHeaderAndFooter">
      <router-view></router-view>
    </div>
    <router-view v-if="!showHeaderAndFooter"></router-view>
    <Footer v-if="showHeaderAndFooter"></Footer>
  </div>

</template>

<script>
  import Header from "@/components/Header"
  import Footer from "@/components/Footer"
  export default {
    computed:{
      showHeaderAndFooter(){
        return this.$route.path != '/login' && this.$route.path != '/register';
      }
    },
    components:{
      Header,
      Footer
    }
  }
</script>

<style>
  .fixedPT{
    padding-top:90px;
  }
</style>



