<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="(i,index) in swiperData" :key="index">
        <a :href="i.url" target="_blank">
          <img :src="$store.state.URL.IMG +i.img" width="1220px" height="554px">
        </a>
      </div>
    </div>
    <div class="swiper-pagination"></div>
  </div>
</template>

<script>
  import axios from "axios"
  export default {
    name:"轮播图",
    data(){
        return{
          swiperData:[]
        }
    },
    mounted(){
      axios.get(`${this.$store.state.URL.API}/banners`).then((resp)=>{
        this.swiperData = resp.data;
        this.$nextTick(()=> {
          new Swiper ('.swiper-container', {
            autoplay: true,
            loop: true,

            pagination: {
              el: '.swiper-pagination',
              clickable :true
            }
          })
        });
      });
    }
  }
</script>

<style scoped>
  .swiper-container{
    width:1220px;
    margin:0 auto;
    border-radius:10px;
  }
  img{
    width:100%;
  }
  .swiper-pagination{
    position: relative;
    top:-80px;
  }
</style>



