import Vue from "vue";
import Vuex from "vuex";
import router from "@/router";

Vue.use(Vuex);

let cartTTId; //用于修复连续添加商品时，计时不重置的bug

export default new Vuex.Store({
  state: {
    URL:{
      // API:"http://101.200.35.203:3000/api",
      // IMG:"http://101.200.35.203:3000/images/"
      API:"http://127.0.0.1:3000/api",
      IMG:"http://127.0.0.1:3000/pic/"
    },
    cartItems:localStorage.cartItems?JSON.parse(localStorage.cartItems):[],
    cartShow:false,
    fixed:false
  },
  mutations:{
    changeFixed(state,f){
      state.fixed = f;
    },
    addToCart(state,item){
      let exist = false;
      for(let i of state.cartItems){
        if(i.productId == item.productId){
          i.num += item.num;
          exist = true;
          break;
        }
      }
      if(!exist){
        state.cartItems.push(item);
      }
      state.cartShow = true;
      localStorage.cartItems = JSON.stringify(state.cartItems);

      //用于修复连续添加商品时，计时不重置的bug
      if(cartTTId){
        clearTimeout(cartTTId);
      }
      cartTTId = setTimeout(()=>{
        state.cartShow = false;
      },1200);
    },
    deleteFromCart(state,id){
      for(let i = 0;i < state.cartItems.length;i ++){
        if(state.cartItems[i].productId == id){
          state.cartItems.splice(i,1);
          break;
        }
      }
      localStorage.cartItems = JSON.stringify(state.cartItems);
    }
  },
  getters:{
    cartTotalNum(state){
      let num = 0;
      for(let p of state.cartItems){
        num += p.num;
      }
      return num;
    },
    cartTotalPrice(state){
      let price = 0;
      for(let p of state.cartItems){
        price += p.num * p.salePrice;
      }
      return price;
    }
  }
});



