<template>
  <span>
    <i class="cart" title="">
      <div class="cart-menu" :class="{'cart-show':$store.state.cartShow}">
        <div class="cart-menu-content">
          <div class="triangle cart-triangle"></div>
          <div class="cart-empty" v-if="$store.state.cartItems.length == 0">
            <img src="../../assets/img/cart-empty.png" alt="" class="cart-empty-img">
            你的购物车竟然是空的！
          </div>
          <div class="cart-not-empty" v-if="$store.state.cartItems.length != 0">
            <ul class="cart-items">
              <li class="cart-item" v-for="(p,i) in $store.state.cartItems" :key="i">
                <img :src="$store.state.URL.IMG + p.productImage" alt="" class="cart-item-img">
                <div class="cart-item-info">
                  <router-link :to="'/productDetail/'+p.productId" class="product-name">{{p.productName}}</router-link>
                  <div class="sub-title">{{p.sub_title}}</div>
                  <div class="price"><span class="yen">&yen;</span>&nbsp;{{p.salePrice}}<span class="product-num">x {{p.num}}</span></div>
                </div>
                <div class="del-btn" @click="deleteFromCart(p.productId)"></div>
              </li>
            </ul>
            <div class="cart-total">
              <Button theme="blue middle" align="right">去购物车</Button>
              <!--<div class="cart-total-num">共 <b>{{$store.getters.cartTotalNum}}</b> 件商品</div>-->
              <div class="cart-total-price">
                <!--合计：<div class="price"><span class="yen">&yen;</span>&nbsp;{{$store.getters.cartTotalPrice}}</div>-->
              </div>
            </div>
          </div>
        </div>
      </div>
    </i>
    <i class="num" :class="{'active':$store.getters.cartTotalNum!=0}">{{$store.getters.cartTotalNum}}</i>
  </span>
</template>

<script>
export default {
  methods:{
    deleteFromCart(id){
      this.$store.commit("deleteFromCart",id);
    }
  }
}
</script>

<style scoped>
  .cart {
    display: inline-block;
    width: 30px;
    height: 20px;
    background: url(../../assets/img/account.png) -150px -22px no-repeat;
    margin-left: 30px;
    position: relative;
    top: 2px;
  }

  .cart:hover {
    background-position: 0 -22px;
  }

  .num {
    display: inline-block;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-size: 12px;
    background: #969696;
    position: relative;
    top: -3px;
    border-radius: 10px;
    color: #fff;
  }

  .num.active {
    background: #eb746b;
  }

  .triangle {
    display: inline-block;
    width: 20px;
    height: 8px;
    background: url(../../assets/img/account.png) -49px -43px no-repeat;
    margin-left: 30px;
    position: absolute;
  }

  .triangle.cart-triangle {
    top: -8px;
    left: 264px;
  }

  .cart-menu{
    margin-top: 20px;
    padding-top: 16px;
    width:360px;
    position: absolute;
    left: -288px;
    display: none;
    cursor:auto;
  }

  .cart:hover .cart-menu{
    display: block;
  }

  .cart-menu-content{
    position: relative;
    z-index:9999;
    box-shadow:0 20px 40px rgba(0, 0, 0, 0.15);
    display: block;
    line-height: 1em;
    text-align: center;
    color: #757575;
    font-size: 12px;
    border-radius: 8px;
    background: #fff;
    width: 360px;
    height: auto;
  }

  .cart-empty{
    height:333px;
    text-align:center;
    position: relative;
    color: #333333;
    font-size: 16px;
    padding-top:187px;
  }

  .cart-empty-img{
    position:absolute;
    width:76px;
    height:62px;
    top:50%;
    left:50%;
    margin-top:-50px;
    margin-left:-38px; ;
  }

  .cart{
    cursor:pointer;
  }

  .fixed .cart:hover{
    background-position: -210px -22px;
  }

  .cart-not-empty{
    text-align: left;
  }

  .cart-item{
    padding:20px;
    border-top: 1px solid #f0f0f0;
    overflow: hidden;
    position: relative;
  }

  .cart-item:hover{
    background: #fbfbfb;
  }

  .cart-item:first-of-type{
    border:0;
    border-radius:8px 8px 0 0;
  }

  .cart-item-img{
    width:80px;
    height:80px;
    border:1px solid #eee;
    float:left;
  }

  .cart-item-info{
    float:left;
    margin-left:18px;
    font-size:14px;
  }

  .sub-title{
    padding-top:10px;
    height:54px;
    width:192px;
    line-height:1.2em;
  }

  .cart-item .price {
    color: #e4393c;
    font-size: 14px;
    font-weight: bold;
  }

  .cart-item .yen {
    font-size: 12px;
  }

  .product-num{
    font-size:12px;
    color:#cacaca;
    margin-left:10px;
  }

  .cart-total{
    padding:20px;
    background: #f7f7f7;
    border-top:#f0f0f0;
    border-radius:0 0 8px 8px;
  }

  .cart-total-num{
    color:#c1c1c1;
    margin-bottom:4px;
  }

  .del-btn{
    cursor: pointer;
    width: 20px;
    height: 20px;
    background: url(../../assets/img/account.png) -50px -60px no-repeat;
    position: absolute;
    top:50px;
    right:16px;
  }

  .del-btn:hover{
    background-position: -75px -60px;
  }

  .cart-total-price{
    font-size:14px;
    color:#6f6f6f;
    height:20px;
    line-height: 20px;
  }

  .cart-total-price .price {
    color: #e4393c;
    font-size: 18px;
    font-weight: bold;
    display: inline-block;
  }

  .cart-total-price .yen {
    font-size: 12px;
  }

  .cart-show{
    display: block;
  }

  .product-name{
    overflow: hidden;
    text-overflow:ellipsis;
    white-space: nowrap;
    width:192px;
    display: inline-block;
  }
</style>



