<template>
  <div class="home">
    <HeaderNav></HeaderNav>
    <Banner></Banner>
    <Hots :items="HotsData"></Hots>
    <Recommend v-for="(r,i) in RecommendData" :item="r" :key="i"></Recommend>
  </div>
</template>

<script>
import axios from "axios"
import Banner from "@/components/Banner"
import Hots from "@/components/Hots"
import Recommend from "@/components/Recommend"
import HeaderNav from "@/components/HeaderNav"
export default {
  components:{
    Banner,
    Hots,
    Recommend,
    HeaderNav
  },
  created(){
    axios.get(`${this.$store.state.URL.API}/hots`).then((resp)=>{
      this.HotsData = resp.data;
    });
    axios.get(`${this.$store.state.URL.API}/recommends`).then((resp)=>{
      this.RecommendData = resp.data;
    });
  },
  data(){
      return {
        RecommendData:[],
        HotsData:[]
      }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



