<template>
  <div class="btn" :class="theme" :style="style" @click="$emit('btn_click')"><slot></slot></div>
</template>

<script>
export default {
  props:["theme","width","align"],
  data(){
      return {
        style:{
          "width":"100px"
        }
      }
  },
  created(){
      if(this.width){
          this.style["width"] = this.width;
      }
      if(this.align == "right"){
          this.style["float"] = "right";
      }
  }
}
</script>

<style scoped>
  .btn{
    display: inline-block;
    height:30px;
    border-radius:4px;
    font-size: 12px;
    line-height: 30px;
    text-align:center;
    cursor:pointer;
  }

  .white{
    border: 1px solid #e1e1e1;
    color: #646464;
    background-color: #f9f9f9;
  }

  .white:hover{
    background-color: #eee;
  }

  .blue{
    border: 1px solid #5c81e3;
    color: #fff;
    background-color: #678ee7;
  }

  .blue:hover{
    background-color: #6c8cd4;
  }

  .gray{
    border: 1px solid #d1d1d1;
    background-color: #fff;
    color: #646464;
  }

  .gray:hover{
    background-color: #ccc;
    color:#fff;
  }

  .circle{
    width:34px !important;
    height:37px;
  }

  .btn.disabled{
    cursor:not-allowed;
  }

  .big{
    width: 145px !important;
    height: 50px !important;
    line-height: 50px !important;
  }

  .middle{
    width: 108px !important;
    height: 40px !important;
    line-height: 40px !important;
  }
</style>



